<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/SystemList' }">系统介绍</el-breadcrumb-item>
        <el-breadcrumb-item>添加系统介绍</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-form ref="form" label-width="120px">
        <div style="height: 460px;">
          <quill-editor
            style="height: 400px;"
            v-model="content"
            ref="myQuillEditor"
            :options="editorOption"
            @blur="onEditorBlur($event)"
            @focus="onEditorFocus($event)"
            @change="onEditorChange($event)"
          ></quill-editor>
        </div>

        <el-form-item>
          <el-button type="primary" size="mini" @click="onSubmit">立即添加</el-button>
          <el-button size="mini">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor"; //调用编辑器

import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

export default {

  data() {
    return {
      content: "",
      editorOption: {},
      toolbar:[
              ['bold', 'italic', 'underline', 'strike'],    //加粗，斜体，下划线，删除线
              ['blockquote', 'code-block'],     //引用，代码块


              [{ 'header': 1 }, { 'header': 2 }],        // 标题，键值对的形式；1、2表示字体大小
              [{ 'list': 'ordered'}, { 'list': 'bullet' }],     //列表
              [{ 'script': 'sub'}, { 'script': 'super' }],   // 上下标
              [{ 'indent': '-1'}, { 'indent': '+1' }],     // 缩进
              [{ 'direction': 'rtl' }],             // 文本方向


              [{ 'size': ['small', false, 'large', 'huge'] }], // 字体大小
              [{ 'header': [1, 2, 3, 4, 5, 6, false] }],     //几级标题


              [{ 'color': [] }, { 'background': [] }],     // 字体颜色，字体背景颜色
              [{ 'font': [] }],     //字体
              [{ 'align': [] }],    //对齐方式


              ['clean'],    //清除字体样式
              ['image']    //上传图片、上传视频

            ]
          ,
          theme:'snow'
    };
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  components: {
    quillEditor
  },
  methods: {
    onEditorReady(editor) {
      // 准备编辑器
    },
    onEditorBlur() {}, // 失去焦点事件
    onEditorFocus() {}, // 获得焦点事件
    onEditorChange(res) {}, // 内容改变事件
    // 转码
    escapeStringHTML(str) {
      str = str.replace(/&lt;/g, "<");
      str = str.replace(/&gt;/g, ">");
      return str;
    },
    urlencode(str) {
      str = (str + "").toString();

      return encodeURIComponent(str)
        .replace(/!/g, "%21")
        .replace(/'/g, "%27")
        .replace(/\(/g, "%28")
        .replace(/\)/g, "%29")
        .replace(/\*/g, "%2A")
        .replace(/%20/g, "+");
    },
    onSubmit() {
      let _this = this;

      let datas = {
        content: _this.content
      };

      console.log(datas);
      this.axios.post("/api/v1/conf/system/info/add", datas)
        .then(function(response) {
          console.log(response);
          if (response.data.code === 200) {
            _this.$router.push({
              path: "/SystemList"
            });
          }
        })
        .catch(function(error) {
          console.log(error);
        });

    },
    handleChange(value) {
      console.log(value);
    }
    /*二级联动选择地区*/
  },
  created: function() {}
};
</script>

<style>
.bm-view {
  height: 200px;
  width: 300px;
}
</style>



