<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right"><el-breadcrumb-item>用户列表</el-breadcrumb-item></el-breadcrumb>
    </div>
    <div class="contents">
      <el-row><el-button size="mini" type="success" @click="add">添加 +</el-button></el-row>
      <div style="display: flex;margin-top: 10px;">
        <el-row :gutter="20">
          <el-col :span="20"><el-input placeholder="请输入名称" v-model="names" size="mini" clearable></el-input></el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="16"><el-input placeholder="请输入账号" v-model="account" size="mini" clearable></el-input></el-col>
          <el-col :span="2"><el-button size="mini" type="success" @click="search">搜索</el-button></el-col>
        </el-row>
      </div>

      <el-table :data="tableData" border center :stripe="true" size="mini" empty-text style="width: 100%;margin-top: 20px;">
        <el-table-column fixed type="index" label="序号" width="60"></el-table-column>
        <el-table-column fixed prop="id" label="id" width="120"></el-table-column>
        <el-table-column fixed prop="nick" label="昵称" width="120"></el-table-column>
        <el-table-column fixed prop="account" label="账号" :show-overflow-tooltip="true" width="100"></el-table-column>
        <el-table-column fixed prop="password" label="密码" ></el-table-column>
        <el-table-column fixed="right" label="操作" >
          <template slot-scope="scope">
            <template>
              <el-button size="mini" type="success" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope)">删除</el-button>
            </template>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align:center;padding-top: 20px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="1"
          :page-size="10"
          layout="total, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      names: '',
      account: '',
      currentPage4: 1,
      tableData: [],
      options: [],
      value: '',
      total: 0,
      pageNum: 0,
      pageSize: 10
    }
  },

  methods: {
    search () {
      this.pageNum = 0
      this.pageSize = 10
      this.getStore()
    },
    add(){
      this.$router.push({
        path: '/administratorAdd'
      })
    },
    getStore () {
      var _this = this
      var data = {
        pageNum: _this.pageNum,
        pageSize: _this.pageSize,
        nick: _this.names,
        account: _this.account
      }

      this.axios
        .post('/api/v1/admin/query/page', data)
        .then(function (response) {
          if (response.data.code === 200) {
            _this.tableData = response.data.data.datas
            _this.total = response.data.data.totalCount
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    handleCurrentChange (val) {
      this.pageNum = `${val}` - 1
      this.getStore()
    },
    handleSizeChange (val) {
      this.pageSize = `${val}`
      this.getStore()
    },

    handleEdit (index, row) {
     this.$router.push({
       name: 'AdministratorUpdate',
       params: row
     })
    },
    handleDelete (index, row) {
      let _this = this
      this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      })
        .then(() => {
          row.row.status = 1
          let data = {
            id: row.row.id
          }
          _this.axios
            .post('/api/v1/admin/delete', data)
            .then(function (response) {
              if (response.data.code === 200) {
                _this.tableData.splice(index, 1)
                _this.$message({
                  type: 'success',
                  message: '删除成功'
                })
                _this.getStore()
              }
            })
            .catch(function (error) {
              console.log(error)
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    }
  },
  mounted: function () {
    this.getStore()
  }
}
</script>

<style>
#operations {
  text-align: right;
}

.demo {
}
</style>



