<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/noticeList' }">系统公告</el-breadcrumb-item>
        <el-breadcrumb-item>添加公告</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-form ref="form" label-width="120px">
        <el-form-item label="公告内容">
          <el-input type="textarea" v-model="content" style="width: 500px;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="mini" @click="onSubmit">立即添加</el-button>
          <el-button size="mini">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: ""
    };
  },
  methods: {
    onSubmit() {
      let _this = this;

      let data = {
        content: _this.content
      };
      console.log(data);
      this.axios
        .post("/api/v1/sys/notice/", data)
        .then(res => {
          if (res.data.code != 200) {
            _this.$message({
              message: "失败",
              type: "warning"
            });
          } else {
            _this.$message({
              message: "成功",
              type: "success"
            });
            _this.$router.push({
              path: "/noticeList"
            });
          }
        });
    },
    handleChange(value) {
      console.log(value);
    }
    /*二级联动选择地区*/
  },
  created: function() {}
};
</script>

<style>
.bm-view {
  height: 200px;
  width: 300px;
}
</style>



