<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right"><el-breadcrumb-item>用户列表</el-breadcrumb-item></el-breadcrumb>
    </div>
    <div class="contents">
      <div style="display: flex;margin-top: 10px;">
        <el-row :gutter="20">
          <el-col :span="20"><el-input placeholder="请输入名称" v-model="names" size="mini" clearable></el-input></el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="16"><el-input placeholder="请输入账号" v-model="account" size="mini" clearable></el-input></el-col>
          <el-col :span="2"><el-button size="mini" type="success" @click="search">搜索</el-button></el-col>
        </el-row>
      </div>

      <el-table :data="tableData" border center :stripe="true" size="mini" empty-text style="width: 100%;margin-top: 20px;">
        <el-table-column fixed type="index" label="序号" width="60"></el-table-column>
        <el-table-column fixed prop="nick" label="名称" width="120"></el-table-column>
        <el-table-column fixed prop="id" label="账号" :show-overflow-tooltip="true" width="100"></el-table-column>
        <el-table-column fixed prop="parentId" label="邀请人ID" width="120"></el-table-column>
        <el-table-column fixed prop="tel" label="电话" width="120"></el-table-column>
        <el-table-column fixed label="注册时间">
          <template slot-scope="scope">
            {{ new Date(scope.row.regTime) }}
          </template>
        </el-table-column>

        <el-table-column fixed prop="level" label="等级" width="100"></el-table-column>
        <el-table-column fixed prop="showInfo" label="是否显示" width="80"></el-table-column>
        <el-table-column fixed label="图片">
          <template slot-scope="scope">
            <img :src="scope.row.headImg" style="height: 30px; width: 30px;border-radius: 50%;" />
          </template>
        </el-table-column>
        <el-table-column align="center" fixed label="查看佣金" width="100">
          <template slot-scope="scope">
            <span style="color:#ff0000;font-size:16px;text-decoration:underline " @click="commission(scope.row)">{{ scope.row.balance }}</span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="查看拥有的卡片" width="180">
          <template slot-scope="scope">
            <template>
              <el-button size="mini" type="primary" round @click="card(scope.row)">查看</el-button>
            </template>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align:center;padding-top: 20px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="1"
          :page-size="10"
          layout="total, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      names: '',
      account: '',
      currentPage4: 1,
      tableData: [],
      options: [],
      value: '',
      total: 0,
      pageNum: 0,
      pageSize: 10
    }
  },

  methods: {
    search () {
      this.pageNum = 0
      this.pageSize = 10
      this.getStore()
    },
    getStore () {
      var _this = this
      var data = {
        pageNum: _this.pageNum,
        pageSize: _this.pageSize,
        nick: _this.names,
        id: _this.account
      }

      this.axios
        .post('/api/v1/user/query/page', data)
        .then(function (response) {
          if (response.data.code === 200) {
            _this.tableData = response.data.data.datas
            _this.total = response.data.data.totalCount
          }
        })
    },
    handleCurrentChange (val) {
      this.pageNum = `${val}` - 1
      this.getStore()
    },
    handleSizeChange (val) {
      this.pageSize = `${val}`
      this.getStore()
    },
    commission (row) {
      this.$router.push({
        name: 'CommissionList',
        params: row
      })
    },
    card (row) {
      this.$router.push({
        name: 'CardList',
        params: row
      })
    }
  },
  mounted: function () {
    this.getStore()
  }
}
</script>

<style>
#operations {
  text-align: right;
}

.demo {
}
</style>



