<template>
	<div><div id="Five" style="width: 100%; height: 400px"></div></div>
</template>
<script>
export default {
	data() {
		return {
		};
	},
	mounted() {
		this.pieChart();
	},
	created: function() {
		let height = document.body.clientHeight;
	},
	methods: {
		pieChart() {
			let myChart = this.$echarts.init(document.getElementById('Five'));
			// app.title = '多 Y 轴示例';
			var colors = ['#5793f3', '#d14a61', '#1e90ff'];
			let option = {
				tooltip: {
					trigger: 'axis'
				},
				legend: {
					data: ['邮件营销', '联盟广告', '视频广告', '直接访问', '搜索引擎']
				},
				
				grid: {
					left: '3%',
					right: '4%',
					bottom: '3%',
					containLabel: true
				},
				xAxis: [
					{
						type: 'category',
						boundaryGap: false,
						data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
					}
				],
				yAxis: [
					{
						type: 'value'
					}
				],
				series: [
					{
						name: '邮件营销',
						type: 'line',
						stack: '总量',
						areaStyle: { normal: {} },
						data: [120, 132, 101, 134, 90, 230, 210]
					},
					{
						name: '联盟广告',
						type: 'line',
						stack: '总量',
						areaStyle: { normal: {} },
						data: [220, 182, 191, 234, 290, 330, 310]
					},
					{
						name: '视频广告',
						type: 'line',
						stack: '总量',
						areaStyle: { normal: {} },
						data: [150, 232, 201, 154, 190, 330, 410]
					},
					{
						name: '直接访问',
						type: 'line',
						stack: '总量',
						areaStyle: { normal: {} },
						data: [320, 332, 301, 334, 390, 330, 320]
					},
					{
						name: '搜索引擎',
						type: 'line',
						stack: '总量',
						label: {
							normal: {
								show: true,
								position: 'top'
							}
						},
						areaStyle: { normal: {} },
						data: [820, 932, 901, 934, 1290, 1330, 1320]
					}
				]
			};

			myChart.setOption(option);
		}
	}
};
</script>

<style></style>



