<template>
	<div>
		<div id="three" style="width: 100%; height: 300px"></div>
	</div>

</template>
<script>
	export default {
		data() {
			return {
			}
		},
		mounted() {
			this.pieChart();
		},
		created: function () {
			let height = document.body.clientHeight;
		},
		methods: {

			pieChart() {
				let myChart = this.$echarts.init(document.getElementById('three'));
				let option = {
					tooltip: {
						formatter: "{a} <br/>{b} : {c}%"
					},
					series: [{
						name: '业务指标',
						type: 'gauge',
						detail: {
							formatter: '{value}%'
						},
						axisLine: {
							show: true,
							lineStyle: { // 属性lineStyle控制线条样式
								color: [ //表盘颜色
									[0.3, "#999999"], //0-50%处的颜色
									[0.7, "#FF9618"], //51%-70%处的颜色
									[0.9, "#FFED44"], //70%-90%处的颜色
									[1, "#DA462C"] //90%-100%处的颜色
								],
								width: 10 //表盘宽度
							}
						},
						data: [{
							value: 10,
							name: '完成率',
							color: '#ffffff'
						}]
					}]
				};

					option.series[0].data[0].value = (Math.random() * 100).toFixed(2) - 0;
					myChart.setOption(option, true);
			}
		}
	}
</script>

<style>

</style>



