<template>
  <div class="crad-contents">
    <el-form ref="form" label-width="120px">
      <el-form-item label="卡片ID">
        <el-input type="text" v-model="requestData.cardNo" style="width: 500px;height: 120px;"></el-input>
      </el-form-item>
      <div class="main-flex-center">
        <div>
          <el-button type="primary" size="mini" @click="onSubmit">立即销卡</el-button>
          <el-button size="mini" @click="cancle">取消</el-button>
        </div>
      </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        requestData: {
          cardNo: ''
        }
      };
    },
    methods: {
      cancle(){
        this.requestData.cardNo = ''
      },
      onSubmit() {
        let _this = this;
        this.axios.post('/api/v1/user/card/consume', _this.requestData)
          .then(function(response) {
            if (response.data.code === 200) {
              _this.$message({
                message: "销卡成功",
                type: "success"
              });
            }
          })
      },
    },
    mounted() {

    }
  };
</script>

<style scoped>
  .crad-contents {
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFFFFF;
  }
  .main-flex-center{
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>



