<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item to="/storeList">店铺管理</el-breadcrumb-item>
        <el-breadcrumb-item>店铺卡片管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-row>
        <el-button size="mini" type="success" @click="add">添加 +</el-button>
      </el-row>
      <div style="display: flex;margin-top: 10px;">
        <el-row :gutter="20">
          <el-col :span="16">
            <el-input placeholder="请输入内容" v-model="searchKey" size="mini" clearable></el-input>
          </el-col>
          <el-col :span="2">
            <el-button size="mini" type="success" @click="search">搜索</el-button>
          </el-col>
        </el-row>

      </div>

      <el-table :data="tableData" border :stripe="true" size="mini" empty-text style="width: 100%;margin-top: 20px;">
        <el-table-column fixed type="index" label="序号" width="60"></el-table-column>
        <el-table-column fixed prop="title" label="名称" width="120"></el-table-column>
        <el-table-column fixed prop="price" label="卡片价格" width="100"></el-table-column>
        <el-table-column fixed prop="ratioCost" label="比率: 成本 %" :show-overflow-tooltip="true" width="100"></el-table-column>
        <el-table-column fixed prop="ratioRoyalty" label="比率: 提成 %" width="100"></el-table-column>
        <el-table-column fixed prop="times" label="消费次数" width="100"></el-table-column>
        <el-table-column fixed prop="effectiveTime" label="有效时间" width="120"></el-table-column>
        <el-table-column fixed prop="cardImg" label="图片"></el-table-column>
        <el-table-column fixed="right" label="操作">
          <template slot-scope="scope">
            <template>
              <el-button size="mini" type="success" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope)">删除</el-button>
            </template>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align:center;padding-top: 20px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="1"
          :page-size="10" layout="total, prev, pager, next, jumper" :total="total"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      searchKey: '',
      currentPage4: 1,
      tableData: [],
      options: [],
      value: '',
      total: 0,
      pageNum: 0,
      pageSize: 10,
      query: '',
      cardImg: ''
    }
  },
  methods: {
    search () {

    },
    getStoreCard () {
      var _this = this
      var data = {
        pageNum: _this.pageNum,
        pageSize: _this.pageSize,
        storeId: _this.query.id
      }
      this.axios
        .post('/api/v1/card/query/page', data)
        .then(function (response) {
          if (response.data.code === 200) {
            _this.tableData = response.data.data.datas
            _this.total = response.data.data.totalCount
          }
        })
    },
    handleCurrentChange (val) {
      this.pageNum = `${val}` - 1
      console.log(this.pageNum)
      this.getStoreCard()
    },
    handleSizeChange (val) {
      this.pageSize = `${val}`
      this.getStoreCard()
    },

    handleEdit (index, row) {
      this.$router.push({
        name: 'StoreCardUpdate',
        params: row
      })
    },
    handleDelete (index, row) {
      let _this = this
      this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      })
        .then(() => {
          let data = {
            id: row.row.id
          }
          _this.axios.post('/api/v1/card/delete', data)
            .then(function (response) {
              if (response.data.code === 200) {
                _this.tableData.splice(index, 1)
                _this.$message({
                  type: 'success',
                  message: '删除成功'
                })
                _this.getStoreCard()
              }
            })
            .catch(function (error) {
              console.log(error)
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    add () {
      let _this = this
      this.$router.push({
        name: 'StoreCardAdd',
        params: _this.query
      })
    },
    handleClick (row) {
      console.log(row)
    }
  },
  mounted: function () {
    this.query = this.$route.params
    this.getStoreCard()
  }
}
</script>

<style>
  #operations {
    text-align: right;
  }

  .demo {}
</style>



