<template>
	<div>
		<div class="header">
			<img :src="src" class="header-logo" />
			<span class="header-title">新概念分销后台管理系统</span>
		</div>
		<div style="float: right;">
			<el-menu
				class="el-menu-demo"
				mode="horizontal"
				@select="handleSelect"
				background-color="#6ea6e8"
				text-color="#fff"
				active-text-color="#ffd04b"
			>
				<el-menu-item index="1">处理中心</el-menu-item>

				<el-menu-item index="2">消息中心</el-menu-item>
				<el-submenu index="4">
					<template slot="title">
						个人中心
					</template>
					<el-menu-item index="2-1" >个人信息</el-menu-item>
					<el-menu-item index="2-2" @click="exit">退出</el-menu-item>
				</el-submenu>
			</el-menu>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			activeIndex: 1,
			src: '../static/img/logo/logo.png'
		};
	},
	methods: {
		handleSelect(key, keyPath) {
			console.log(key, keyPath);
		},
		personInfo(){
			this.$router.push('/personInfo');
		},
		exit() {
			this.$store.commit('logout',false);
			this.$router.push('/login');
		}
	}
};
</script>

<style>
.header {
	float: left;
	line-height: 60px;
	padding-left: 20px;
	display: flex;
}
.header-logo {
	height: 40px;
	width: 40px;
	border-radius: 20px;
	margin-top: 10px;
}
.header-title {
	padding-left: 10px;
	color: #ffffff;
	font-size: 24px;
	font-weight: 600;
	/* font-family: cursive; */
	/* letter-spacing: 3px; */
}
</style>



