import axios from 'axios'
import qs from 'qs'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

axios.defaults.timeout = 5000 // 设置超时时间
axios.defaults.baseURL = 'http://pay.loyom.com' // 这是调用数据接口
// process.env.BASE_URL
/** **** 创建axios实例 ******/
const service = axios.create({
  baseURL: 'http://pay.loyom.com', // api的base_url
  timeout: 5000 // 请求超时时间
})
/** **** request拦截器==>对请求参数做处理 ******/
service.interceptors.request.use(config => {
  // app.$vux.loading.show({
  //     text: '数据加载中……'
  // });

  (config.method === 'post')?(config.data = qs.stringify({ ...config.data })):(config.params={...config.params})
  config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
  return config
}, error => { // 请求错误处理
  ElementUI.Message({
    message: error,
    type: 'error'
  })
  Promise.reject(error)
})

/** **** respone拦截器==>对响应做处理 ******/
service.interceptors.response.use(
  response => { // 成功请求到数据
    // 这里根据后端提供的数据进行对应的处理
    if (response.data.code === 200) {
      return response
    } else {
      ElementUI.Message({
        message: response.data.message,
        type: 'error'
      })
    }
  },
  error => { // 响应错误处理
    const text = JSON.parse(JSON.stringify(error)).response.status === 404 ?
      '404' :
      '网络异常，请重试'
    ElementUI.Message({
      message: '失败404',
      type: 'error'
    })
    return Promise.reject(error)
  }
)

export default service



