<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/bannerList' }">广告管理</el-breadcrumb-item>
        <el-breadcrumb-item>添加广告</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-form ref="form" label-width="120px">

        <el-form-item label="广告链接">
          <el-input type="text" v-model="requestData.url" style="width: 500px;"></el-input>
        </el-form-item>
        <el-form-item label="广告图片地址">
          <el-input type="text" v-model="requestData.filePath" style="width: 500px;"></el-input>
        </el-form-item>
        <el-form-item label="广告内容">
          <el-input type="textarea" v-model="requestData.intro" style="width: 500px;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="mini" @click="onSubmit">立即添加</el-button>
          <el-button size="mini">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        requestData: {
          url: '',
          filePath: '',
          intro: '',
          resId: '1',
          userId: '22'
        }
      };
    },
    methods: {
      onSubmit() {
        let _this = this;

        this.axios
          .post('/api/v1/banner/', _this.requestData)
          .then(function(response) {
            if (response.data.code === 200) {
              _this.$message({
                message: "成功",
                type: "success"
              });
              _this.$router.push({
                path: "/bannerList"
              });
            }
          })
      }
    }
  };
</script>

<style>
  .bm-view {
    height: 200px;
    width: 300px;
  }
</style>



