<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item to="/employeeList">用户列表</el-breadcrumb-item>
        <el-breadcrumb-item>用户卡片信息</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <div style="display: flex;">
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <span style="padding-left: 10px;"></span>
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <span style="padding-left: 10px;"></span>
      </div>

      <el-table
        :data="tableData"
        border
        :stripe="true"
        size="mini"
        empty-text
        style="width: 100%;margin-top: 20px;"
      >
        <el-table-column fixed type="index" label="序号" width="60"></el-table-column>
        <el-table-column fixed prop="cardId" label="卡片ID" ></el-table-column>
         <el-table-column fixed  label="购买时间" >
             <template slot-scope="scope">
                   
              <span>{{new Date(scope.row.buyTime)}}</span>
            
          </template>
         </el-table-column>

        <el-table-column fixed  label="过期时间" :show-overflow-tooltip="true">
            <template slot-scope="scope">
                   
              <span>{{new Date(scope.row.expireTime)}}</span>
            
          </template>
        </el-table-column>
        <el-table-column fixed prop="status" label="状态" width="120"></el-table-column>
        <el-table-column fixed prop="usedTimes" label="已消费次数" width="120"></el-table-column>
       
        <el-table-column fixed prop="userId" label="用户ID" width="100"></el-table-column>
        <el-table-column fixed prop="storeId" label="店铺ID" width="100"></el-table-column>
        
        <el-table-column fixed="right" label="操作" width="180">
          <template >
        
            
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align:center;padding-top: 20px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="1"
          :page-size="10"
          layout="total, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentPage4: 1,
      tableData: [],
      options: [],
      value: "",
      total: 0,
      query: "",
      pageNum: 0,
      pageSize: 15
    };
  },
  methods: {
    getCard() {
      var _this = this;
      var data = {
        userId: _this.query.id,
        pageNum: _this.pageNum,
        pageSize: _this.pageSize
      };
      this.axios
        .post("/api/v1/user/card/query/page", data)
        .then(function(response) {
          if (response.data.code === 200) {
            _this.tableData = response.data.data.datas;
            _this.total = response.data.data.totalCount;
          }
        })
    },
    handleCurrentChange(val) {
      var num = `${val}` - 1
      this.getStore(num);
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    }
  },
  mounted: function() {
    this.query = this.$route.params;
    this.getCard();
  }
};
</script>

<style>
#operations {
  text-align: right;
}
</style>



