<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>系统介绍</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-row>
        <el-button size="mini" type="success" @click="add">修改 +</el-button>
      </el-row>

      <el-form ref="form" label-width="120px">
        <div style="height: 500px;margin-top:20px">
          <quill-editor
            style="height: 400px;"
            v-model="content"
            ref="myQuillEditor"
            :options="editorOption"
            @blur="onEditorBlur($event)"
            @focus="onEditorFocus($event)"
            @change="onEditorChange($event)"
          ></quill-editor>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor"; //调用编辑器

import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

export default {
  data() {
    return {
      content: "",
      editorOption: {}
    };
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  components: {
    quillEditor
  },
  methods: {
    onEditorReady(editor) {
      // 准备编辑器
    },
    onEditorBlur() {
      this.editor.enable(false);
    }, // 失去焦点事件
    onEditorFocus() {
      this.editor.enable(false); // 在获取焦点的时候禁用
    }, // 获得焦点事件
    onEditorChange(res) {}, // 内容改变事件
    // 转码
    escapeStringHTML(str) {
      str = str.replace(/&lt;/g, "<");
      str = str.replace(/&gt;/g, ">");
      return str;
    },
    urlencode(str) {
      str = (str + "").toString();

      return encodeURIComponent(str)
        .replace(/!/g, "%21")
        .replace(/'/g, "%27")
        .replace(/\(/g, "%28")
        .replace(/\)/g, "%29")
        .replace(/\*/g, "%2A")
        .replace(/%20/g, "+");
    },

    handleChange(value) {
      console.log(value);
    },
    add() {
      this.$router.push({
        path: "/SystemAdd"
      });
    }
  },
  mounted: function() {
    this.editor.enable(false);
    this.axios
      .post("/api/v1/conf/system/info", {})
      .then(res => {
        if (res.data.code === 200) {
          this.content = res.data.data.itemValue;
        }
      })
      .catch(error => {
        console.log(error);
      });
  }
};
</script>

<style>
.bm-view {
  height: 200px;
  width: 300px;
}
</style>



