<template>
	<el-container>
		<el-header class="headers"><Header></Header></el-header>
		<el-container style="height: 100%;margin-top: 60px;">
			<el-aside width="200px" overflow="auto"><LeftNav></LeftNav></el-aside>
			<el-container>
				<el-main class="main-content">
					<router-view />
				</el-main>
				<!-- <el-footer class="footer">成都麦之芽科技有限公司 Copyright © 蜀ICP备16017060号-1</el-footer> -->
			</el-container>
		</el-container>
	</el-container>
</template>
<script>
import LeftNav from './LeftNav';
import Header from './Header';
export default {
	data() {
		return {
			activeIndex: '1',
			height: '800'
		};
	},
	methods: {},
	created() {
		var _this = this;
		//获取屏幕高度
		var height = document.body.clientHeight;
		_this.height = height - 60;
	},
	components: {
		LeftNav,
		Header
	}
};
</script>

<style>
.headers {
	padding: 0 0 0 0;
	background: #6ea6e8;
	color: #ffffff;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 100;
	width: 100%;
    height: 60px;
    min-width: 600px;
}
.main-content {
	background-color: #ffffff;
	height: 100%;
	padding-left: 0px;
	padding-top: 0px;
	padding-right: 10px;
	display: block;
}
.el-menu--horizontal {
	border-right: none;
	border-bottom: none;
}
.footer {
	background: #444;
	text-align: center;
	line-height: 60px;
	color: #ffffff;
	position: fixed;
	width: 100%;
	bottom: 0;
	right: 0;
	z-index: 99;
}
</style>



