<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>用户订单</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">

      <div style="display: flex;margin-top: 10px;">
        <el-row :gutter="20">
          <el-col :span="16">
            <el-input placeholder="请输入内容" v-model="searchKey" size="mini" clearable></el-input>
          </el-col>
          <el-col :span="2">
            <el-button size="mini" type="success" @click="search">搜索</el-button>
          </el-col>
        </el-row>

      </div>

      <el-table :data="tableData" border center :stripe="true" size="mini" empty-text style="width: 100%;margin-top: 20px;">
        <el-table-column fixed type="index" label="序号" width="60"></el-table-column>
        <el-table-column fixed prop="tradeNo" label="订单号" width="260"></el-table-column>
        <el-table-column fixed prop="userId" label="用户id" :show-overflow-tooltip="true" width="100"></el-table-column>
        <el-table-column fixed prop="storeId" label="店铺id" width="120"></el-table-column>
        <el-table-column fixed prop="cardId" label="卡片id" width="120"></el-table-column>
        <el-table-column fixed label="订单时间" >
          <template slot-scope="scope">
            {{new Date(scope.row.createTime)}}
          </template>
        </el-table-column>

        <el-table-column fixed prop="price" label="金额" width="100"></el-table-column>

        <el-table-column fixed="right" label="状态" width="180">
          <template slot-scope="scope">
            <template>
               <!-- status 状态,0-未付款,1-付款,2-取消,3-超时 -->
               <span v-if="scope.row.status === 0" style="color: #ff0000;"> 未付款 </span>
               <span v-if="scope.row.status === 1" style="color: chartreuse;"> 付款 </span>
               <span v-if="scope.row.status === 2" style="color: darkgoldenrod;"> 取消 </span>
               <span v-if="scope.row.status === 3" style="color: #475669;"> 超时 </span>
            </template>
          </template>
        </el-table-column>

      </el-table>
      <div style="text-align:center;padding-top: 20px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="1"
          :page-size="10" layout="total, prev, pager, next, jumper" :total="total"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        searchKey: '',
        currentPage4: 1,
        tableData: [],
        options: [],
        value: '',
        total: 0,
        pageNum: 0,
        pageSize: 15
      }
    },

    methods: {
      search() {

      },
      getStore() {
        var _this = this
        var data = {
          pageNum: _this.pageNum,
          pageSize: _this.pageSize
        }
        this.axios
          .post('/api/v1/user/order/query/page', data)
          .then(function(response) {
            if (response.data.code === 200) {
              _this.tableData = response.data.data.datas;
              _this.total = response.data.data.totalCount
            }
          })
      },
      handleCurrentChange(val) {
        this.pageNum = `${val}` - 1;
        this.getStore()
      },
      handleSizeChange(val) {
        this.pageSize = `${val}`
        this.getStore()
      }
    },
    mounted: function() {
      this.getStore()
    }
  }
</script>

<style>
  #operations {
    text-align: right;
  }

  .demo {}
</style>



