<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/StoreCard' }">店铺卡片管理</el-breadcrumb-item>
        <el-breadcrumb-item>店铺卡片添加</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-form ref="form" label-width="120px">
        <el-form-item label="卡片标题">
          <el-input size="mini" v-model="form.title" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item label="卡片价格">
          <el-input v-model="form.price" size="mini" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item label="比率: 成本 %">
          <el-input v-model="form.ratioCost" size="mini" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item label="比率: 提成 %">
          <el-input v-model="form.ratioRoyalty" size="mini" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item label="消费次数">
          <el-input v-model="form.times" size="mini" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item label="有效时间">
          <el-col :span="5">
            <el-date-picker type="date" size="mini" placeholder="选择日期" v-model="form.effectiveTime" style="width: 300px;"></el-date-picker>
          </el-col>
        </el-form-item>

        <el-form-item label="图片">
          <el-input v-model="form.cardImg" size="mini" style="width: 300px;"></el-input>
        </el-form-item>

        <el-form-item label="卡片介绍">
          <el-input type="textarea" v-model="form.intro" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="mini" @click="onSubmit">立即添加</el-button>
          <el-button size="mini">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {
        storeId: '',
        intro: '',
        price: '',
        ratioCost: '',
        ratioRoyalty: '', // 店铺简介
        times: '', // 店铺图片
        title: '',
        effectiveTime: ''
      },
      query: ''
    }
  },
  methods: {
    onSubmit () {
      let _this = this
      this.form.effectiveTime = this.fmtTimestamp(this.form.effectiveTime)
      console.log(this.form)
      this.axios.post('/api/v1/card/', this.form)
        .then(function (response) {
          if (response.data.code !== 200) {
            _this.$message({
              message: '失败',
              type: 'warning'
            })
          } else {
            _this.$message({
              message: '成功',
              type: 'success'
            })
            _this.$router.push({
              path: '/storeList'
            })
          }
        })
    },
    handleChange (value) {
      console.log(value)
    },
    fmtTimestamp: function (time) {
     	 return new Date(time).getTime()
    }
  },
  mounted: function () {
    let query = this.$route.params
    console.log(query)
    this.form.storeId = query.id
  }
}
</script>

<style>
  .bm-view {
    height: 200px;
    width: 300px;
  }
</style>



