<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/administratorList' }">管理员列表</el-breadcrumb-item>
        <el-breadcrumb-item>管理员修改</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-form label-width="120px">
        <el-form-item label="名称">
          <el-input size="mini" v-model="form.nick" style="width: 300px;"></el-input>
        </el-form-item>

        <el-form-item label="账号">
          <el-input v-model="form.account" size="mini" style="width: 300px;"></el-input>
        </el-form-item>

        <el-form-item label="密码">
          <el-input v-model="form.password" size="mini" type="password" style="width: 300px;"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" size="mini" @click="onSubmit">立即修改</el-button>
          <el-button size="mini" @click="cancle">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        nick: "",
        account: "",
        password: "",
      },
    };
  },
  methods: {
    cancle(){
      this.form.account = ''
      this.form.password = ''
    },
    onSubmit() {
      let _this = this;
      let data = this.form;
      this.axios.post("/api/v1/admin/update/",data).then(res => {
        if (res.data.code != 200) {
          _this.$message({
            message: "失败",
            type: "warning"
          });
        } else {
          _this.$message({
            message: "成功",
            type: "success"
          });
          _this.$router.push({
            path: "/administratorList"
          });
        }
      });
    }
  },
  mounted () {
    this.form = this.$route.params
  }
};
</script>

<style>
.bm-view {
  height: 200px;
  width: 300px;
}
</style>



