<template>
	<el-row class="tac">
		<el-col>
			<el-menu
				default-active="1"
				class="el-menu-vertical-demo"
				style="height: 100%;"
				@open="handleOpen"
				@close="handleClose"
				background-color="#444"
				text-color="#8a8d96"
				active-text-color="#ffd04b"
			>
				<el-menu-item index="1" @click="getNav('dashborad')">
					<i class="el-icon-menu"></i>
					<span slot="title">主页</span>
				</el-menu-item>
        <el-menu-item index="2" @click="getNav('AdministratorList')">
        	<i class="el-icon-service"></i>
        	<span slot="title">管理员管理</span>
        </el-menu-item>
        
				<el-submenu index="3">
					<template slot="title">
						<i class="el-icon-user-solid"></i>
						<span>用户管理</span>
					</template>
					<el-menu-item-group>
						<el-menu-item index="3-1" @click="getNav('employeeList')">
							<i class="el-icon-user-solid"></i>
							<span>用户列表</span>
						</el-menu-item>
            <el-menu-item index="3-2" @click="getNav('employeeOrders')">
            	<i class="el-icon-user-solid"></i>
            	<span>用户订单</span>
            </el-menu-item>
					</el-menu-item-group>
				</el-submenu>
        <el-submenu index="4">
        	<template slot="title">
        		<i class="el-icon-goods"></i>
        		<span>店铺管理</span>
        	</template>
        	<el-menu-item-group>
            <el-menu-item index="4-1" @click="getNav('storeList')">
            	<i class="el-icon-menu"></i>
            	<span>店铺列表</span>
            </el-menu-item>
        		<el-menu-item index="4-2" @click="getNav('feedbackList')">
        			<i class="el-icon-user-solid"></i>
        			<span>加盟申请</span>
        		</el-menu-item>
        	</el-menu-item-group>
        </el-submenu>
        <el-menu-item index="5" @click="getNav('cancelCard')">
        	<i class="el-icon-circle-close"></i>
        	<span slot="title">店铺销卡</span>
        </el-menu-item>
				<el-menu-item index="6" @click="getNav('bannerList')">
					<i class="el-icon-mobile-phone"></i>
					<span slot="title">广告管理</span>
				</el-menu-item>
				<el-menu-item index="7" @click="getNav('systemList')">
					<i class="el-icon-setting"></i>
					<span slot="title">系统介绍</span>
				</el-menu-item>
        <el-menu-item index="8" @click="getNav('noticeList')">
        	<i class="el-icon-message-solid"></i>
        	<span slot="title">系统公告</span>
        </el-menu-item>
			</el-menu>
		</el-col>
	</el-row>
</template>

<script>
export default {
  data () {
    return {

    }
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    getNav (data) {
      this.$router.push('/' + data + '')
    },
    handleClose () {}
  },
  created () {
  },
  watch: {}
}
</script>

<style>
.el-submenu .el-menu-item {
	height: 40px;
	line-height: 40px;
	padding-left: 60px;
	min-width: 160px;
}
.el-menu-item-group__title {
	padding: 0rem;
	line-height: normal;
	font-size: 12px;
	color: #909399;
}
.el-menu-vertical-demo {
	min-height: 100%;
	position: fixed;
	width: 200px;
}
</style>



