import Vue from 'vue'

import Router from 'vue-router'
import store from '../store/index.js'

import Dashborad from '@/page/dashborad/dashborad'
import Login from '@/page/login/login'
import EmployeeList from '@/page/employee/employeeList'
import EmployeeAdd from '@/page/employee/employeeAdd'
import StoreList from '@/page/store/storeList'
import StoreAdd from '@/page/store/storeAdd'
import StoreUpdate from '@/page/store/storeUpdate'
import StoreCard from '@/page/store/storeCard'
import StoreCardAdd from '@/page/store/storeCardAdd'
import StoreCardUpdate from '@/page/store/storeCardUpdate'
import CancelCard from '@/page/store/cancelCard'

import Main from '@/components/main'
import NoticeList from '@/page/notice/NoticeList'
import AddNotice from '@/page/notice/addNotice'
import CardList from '@/page/card/cardList'
import BalanceList from '@/page/balance/balanceList'
import CommissionList from '@/page/commission/commissionList'
import WithdrawalList from '@/page/withdrawal/withdrawalList'
import SystemList from '@/page/system/systemList'
import SystemAdd from '@/page/system/systemAdd'
import BannerList from '@/page/banner/list'
import BannerAdd from '@/page/banner/add'
import EmployeeOrders from '@/page/employee/orders'

import FeedbackList from '@/page/feedback/list'
import AdministratorList from '@/page/administrator/list'
import AdministratorAdd from '@/page/administrator/add'
import AdministratorUpdate from '@/page/administrator/update'



Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [{
      path: '/',
      redirect: '/login',
      meta: {
        requireAuth: true
      }
    }, {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/main',
      name: 'Main',
      component: Main,
      meta: {
        requireAuth: true
      },
      redirect: '/dashborad', // 设置默认为 welcome
      children: [{
          path: '/withdrawalList',
          name: 'WithdrawalList',
          component: WithdrawalList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/commissionList',
          name: 'CommissionList',
          component: CommissionList,
          meta: {
            requireAuth: true
          }

        },
        {
          path: '/balanceList',
          name: 'BalanceList',
          component: BalanceList,
          meta: {
            requireAuth: true
          }

        },
        {
          path: '/dashborad',
          name: 'Dashborad',
          component: Dashborad,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/cardList',
          name: 'CardList',
          component: CardList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/noticeList',
          name: 'NoticeList',
          component: NoticeList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/addNotice',
          name: 'AddNotice',
          component: AddNotice,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/employeeList',
          name: 'EmployeeList',
          component: EmployeeList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/employeeAdd',
          name: 'EmployeeAdd',
          component: EmployeeAdd,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/employeeOrders',
          name: 'EmployeeOrders',
          component: EmployeeOrders,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/storeList',
          name: 'StoreList',
          component: StoreList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/storeAdd',
          name: 'StoreAdd',
          component: StoreAdd,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/storeCard',
          name: 'StoreCard',
          component: StoreCard,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/storeCardAdd',
          name: 'StoreCardAdd',
          component: StoreCardAdd,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/storeUpdate',
          name: 'StoreUpdate',
          component: StoreUpdate,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/storeCardUpdate',
          name: 'StoreCardUpdate',
          component: StoreCardUpdate,
          meta: {
            requireAuth: true
          }
        },

        {
          path: '/cancelCard',
          name: 'CancelCard',
          component: CancelCard,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/systemAdd',
          name: 'SystemAdd',
          component: SystemAdd,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/systemList',
          name: 'SystemList',
          component: SystemList,
          meta: {
            requireAuth: true
          }
        },

        {
          path: '/bannerList',
          name: 'BannerList',
          component: BannerList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/bannerAdd',
          name: 'BannerAdd',
          component: BannerAdd,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/feedbackList',
          name: 'FeedbackList',
          component: FeedbackList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/administratorList',
          name: 'AdministratorList',
          component: AdministratorList,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/administratorAdd',
          name: 'AdministratorAdd',
          component: AdministratorAdd,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/administratorUpdate',
          name: 'AdministratorUpdate',
          component: AdministratorUpdate,
          meta: {
            requireAuth: true
          }
        }
      ]
    }

  ]
})

router.beforeEach((to, from, next) => {
  console.log(store.state.hasLogin)
  if (to.matched.some(res => res.meta.requireAuth)) { // 判断是否需要登录权限
    if (store.state.hasLogin) { // 判断是否登录
      next()
    } else { // 没登录则跳转到登录界面
      if (to.path === '/login') { // 如果是登录页面路径，就直接next()
        next()
      } else { // 不然就跳转到登录；
        next('/login')
      }
    }
  } else {
    next()
  }
})

export default router



