<template>
	<div>
		<div id="Six" :style="{width: '100%', height: +BoxHeight+'px'}"></div>
	</div>

</template>
<script>
	export default {
		data() {
			return {
				BoxHeight: ''
			}
		},
		mounted() {
			this.pieChart();
		},
		created: function () {
			let height = document.body.clientHeight;
			this.BoxHeight = height / 3;
		},
		methods: {

			pieChart() {
				let myChart = this.$echarts.init(document.getElementById('Six'));
				// app.title = '极坐标系下的堆叠柱状图';

				let option = {
					angleAxis: {
						type: 'category',
						data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
						z: 10,
						color:'#fff'
					},
					radiusAxis: {},
					polar: {},
					series: [{
						type: 'bar',
						data: [1, 2, 3, 4, 3, 5, 1],
						coordinateSystem: 'polar',
						name: 'A',
						stack: 'a'
					}, {
						type: 'bar',
						data: [2, 4, 6, 1, 3, 2, 1],
						coordinateSystem: 'polar',
						name: 'B',
						stack: 'a',
						
					}, {
						type: 'bar',
						data: [1, 2, 3, 4, 1, 2, 5],
						coordinateSystem: 'polar',
						name: 'C',
						stack: 'a'
					}],
					legend: {
						show: true,
						data: ['A', 'B', 'C'],
						
					}
				};

				myChart.setOption(option);
			}
		}
	}
</script>

<style>

</style>



