import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const ls = window.sessionStorage;

const store = new Vuex.Store({
	state: {
		custom: null,
		token: null,
		hasLogin: ls.getItem('hasLogin'),
		userData: null,
		userData:{}
	},
	mutations: {
		setCustom(state, custom) {
			state.custom = custom;
			console.log(custom);
		},
		loginData(state, userData) {
			state.userData = userData;
			state.hasLogin = true;
		},
		logout(state) {
			state.userData = null;
			state.hasLogin = false;
      ls.removeItem('hasLogin');
		},
		findUserData(state, callback) {
			callback(state.hasLogin, state.userData);
		}
	}
});

export default store



