<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/StoreList' }">店铺管理</el-breadcrumb-item>
        <el-breadcrumb-item>店铺添加</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-form ref="form" label-width="120px">
        <el-form-item label="店铺名称">
          <el-input size="mini" v-model="form.title" style="width: 300px;"></el-input>
        </el-form-item>
        <!-- <el-form-item label="店铺地区">
          <el-select
            style="width: 300px"
            size="mini"
            v-model="form.province"
            placeholder="请选择省份"
            v-on:change="getProv($event)"
          >
            <el-option
              v-for="item in provs"
              :label="item.label"
              :value="item.value"
              :key="item.value"
            ></el-option>
          </el-select>
          <el-select
            style="width: 300px"
            size="mini"
            v-model="form.city"
            placeholder="请选择城市"
            v-on:change="getCity($event)"
          >
            <el-option
              v-for="item in citys"
              :label="item.label"
              :value="item.value"
              :key="item.value"
            ></el-option>
          </el-select>
        </el-form-item>-->
        <el-form-item label="店铺详细地址">
          <el-input v-model="form.address" size="mini" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item>
          <baidu-map
            class="bm-view"
            :center="center"
            :zoom="zoom"
            @ready="handler"
            @rightclick="dblclicks"
          >
            <!-- <bm-marker :position="markerPoint" :dragging="true"></bm-marker> -->
            <bm-marker
              v-if="markerPoint != ''"
              :position="markerPoint"
              :dragging="true"
              animation="BMAP_ANIMATION_BOUNCE"
            ></bm-marker>
          </baidu-map>
        </el-form-item>

        <!-- <el-form-item label="联系人">
          <el-input v-model="form.employee_name" size="mini" style="width: 300px;"></el-input>
        </el-form-item>-->
        <el-form-item label="联系电话">
          <el-input v-model="form.tel" size="mini" style="width: 300px;"></el-input>
        </el-form-item>

        <!-- <el-form-item label="开店时间">
          <el-col :span="5">
            <el-date-picker
              type="date"
              size="mini"
              placeholder="选择日期"
              v-model="form.date1"
              style="width: 300px;"
            ></el-date-picker>
          </el-col>
        </el-form-item>-->

        <!-- <el-form-item label="评分">
          <el-rate v-model="form.levels" show-text></el-rate>
        </el-form-item>-->
        <el-form-item label="店铺性质">
          <el-radio-group v-model="form.type">
            <el-radio :label="1">总店</el-radio>
            <el-radio :label="2">旗舰店</el-radio>
            <el-radio :label="3">分店</el-radio>
            <el-radio :label="4">加盟店</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="店铺介绍">
          <el-input type="textarea" v-model="form.intro" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="mini" @click="onSubmit">立即添加</el-button>
          <el-button size="mini">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import provs from "@/assets/json/provs.json";
import allCity from "@/assets/json/city.json";
export default {
  data() {
    return {
      center: { lng: 0, lat: 0 },
      zoom: 3,
      form: {
        title: "",
        type: "",
        tel: "",
        address: "",
        intro: "", //店铺简介
        shopImg: "", //店铺图片
        latitude: "",
        longitude: ""
      },
      provs: provs,
      citys: [],
      height: "",
      allCity: allCity,
      markerPoint: {}
    };
  },
  methods: {
    dblclicks(res) {
      let datas = {
        lng: res.point.lng,
        lat: res.point.lat
      };
      this.form.latitude = res.point.lat;
      this.form.longitude = res.point.lng;
      this.markerPoint = datas;
      this.center.lng = res.point.lng;
      this.center.lat = res.point.lat;
      this.zoom = 15;
      console.log(this.markerPoint);
    },
    handler({ BMap, map }) {
      console.log(BMap, map);
      this.center.lng = 116.404;
      this.center.lat = 39.915;
      this.zoom = 15;
    },
    onSubmit() {
      let _this = this;
      console.log(this.form);
      let data = this.form;

      this.axios({
        method: "post",
        url: "/api/v1/store/",
        params: data,
        headers: { "Content-Type": "application/x-www-form-urlencoded" }
      }).then(res => {
        if (res.data.code != 200) {
          _this.$message({
            message: "失败",
            type: "warning"
          });
        } else {
          _this.$message({
            message: "成功",
            type: "success"
          });
          _this.$router.push({
            path: "/storeList"
          });
        }
      });
    
    },
    handleChange(value) {
      console.log(value);
    },
    /*二级联动选择地区*/
    getProv: function(prov) {
      let _this = this;
      let tempCity = [];
      this.citys = [];
      //赋值
      for (var val of _this.allCity) {
        if (prov == val.prov) {
          tempCity.push({
            label: val.label,
            value: val.label
          });
        }
      }
      _this.citys = tempCity;
      _this.form.city = _this.citys[0].value;
    },
    getCity: function(city) {
      console.log(city);
      console.log(this.selectCity);
    }
  },
  created: function() {
    let _this = this;
    //获取省份
    // this.axios.get("../../../static/json/provs.json").then(res => {
    //   //赋值
    //   console.log("正在获取省份");
    //   _this.provs = res.data;
    // });
    var height = document.body.clientHeight;
    _this.height = height - 244;
  }
};
</script>

<style>
.bm-view {
  height: 200px;
  width: 300px;
}
</style>



