<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/employeeList' }">用户列表</el-breadcrumb-item>
        <el-breadcrumb-item>用户添加</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-form label-width="120px">
        <el-form-item label="名称">
          <el-input size="mini" v-model="form.nick" style="width: 300px;"></el-input>
        </el-form-item>

        <el-form-item label="账号">
          <el-input v-model="form.account" size="mini" style="width: 300px;"></el-input>
        </el-form-item>

        <el-form-item label="邀请码">
          <el-input v-model="form.parentId" size="mini" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item label="联系电话">
          <el-input v-model="form.tel" size="mini" style="width: 300px;"></el-input>
        </el-form-item>

        <!-- <el-form-item label="等级">
          <el-rate v-model="form.level" show-text></el-rate>
        </el-form-item> -->


        <el-form-item>
          <el-button type="primary" size="mini" @click="onSubmit">立即添加</el-button>
          <el-button size="mini">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        level: "1",
        showInfo: "1",
        tel: "",
        nick: "",
        parentId: "",
        account: ""
      },

      height: ""
    };
  },
  methods: {
    onSubmit() {
      let _this = this;
      let data = this.form;

      this.axios({
        method: "post",
        url: "/api/v1/user/",
        params: data,
        headers: { "Content-Type": "application/x-www-form-urlencoded" }
      }).then(res => {
        if (res.data.code != 200) {
          _this.$message({
            message: "失败",
            type: "warning"
          });
        } else {
          _this.$message({
            message: "成功",
            type: "success"
          });
          _this.$router.push({
            path: "/employeeList"
          });
        }
      });
    }
  },
  created: function() {}
};
</script>

<style>
.bm-view {
  height: 200px;
  width: 300px;
}
</style>



