<template>
  <div class="logins">
    <div class="login-content">
      <div class="login-form">

        <div style="color: #FFFFFF;">
          <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
            <el-form-item label="用户名" prop="account">
              <el-input type="text" v-model="ruleForm2.account" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="pwd">
              <el-input type="password" v-model="ruleForm2.pwd" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item style="text-align: center;">
              <el-button type="primary" style="width: 120px;" @click="submitForm('ruleForm2')">登录</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  const ls = window.sessionStorage;
  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('用户名'));
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('输入密码'));
        } else {
          callback();
        }
      };
      return {
        ruleForm2: {
          account: '',
          pwd: ''
        },
        rules2: {
          account: [{
            validator: validatePass,
            trigger: 'blur'
          }],
          pwd: [{
            validator: validatePass2,
            trigger: 'blur'
          }]
        }
      };
    },
    methods: {
      submitForm(formName) {
        let _this = this;
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.axios
              .post('/api/v1/admin/login', _this.ruleForm2)
              .then(function(response) {
                console.log(response);
                if (response.data.code === 200) {
                  _this.$message({
                    message: "登陆成功",
                    type: "success"
                  });
                  ls.setItem('hasLogin',true);
                  const logins = ls.getItem('hasLogin');
                  _this.$store.commit('loginData',response.data);
                  _this.$router.replace('/dashborad');
                } else {
                  _this.$message({
                    message: response.data.message,
                    type: "warning"
                  });
                }
              })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  };
</script>

<style>
  .logins {
    position: fixed;
    z-index: 999;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-image: url(/static/img/bg.jpg);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .login-content {
    width: 60%;
    height: 60%;
    /* opacity: 0.7; */
    background: rgba(0, 0, 0, .6);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
  }

  .login-form {
    width: 40%;
  }

  .demo-ruleForm {
    color: #FFFFFF;
  }
</style>



