<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>广告管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-row>
        <el-button size="mini" type="success" @click="add">添加 +</el-button>
      </el-row>
      <div style="display: flex;margin-top: 10px;">
        <el-row :gutter="20">
          <el-col :span="16">
            <el-input placeholder="请输入内容" v-model="searchKey" size="mini" clearable></el-input>
          </el-col>
          <el-col :span="2">
            <el-button size="mini" type="success" @click="search">搜索</el-button>
          </el-col>
        </el-row>

      </div>

      <el-table :data="tableData" border :stripe="true" size="mini" empty-text style="width: 100%;margin-top: 20px;">
        <el-table-column fixed type="index" label="序号" width="60"></el-table-column>
        <el-table-column fixed prop="resId" label="文件id"></el-table-column>
        <el-table-column fixed prop="intro" label="介绍"></el-table-column>
        <el-table-column fixed prop="url" label="链接"></el-table-column>
        <el-table-column fixed label="图片">
          <template slot-scope="scope">
            <img :src="scope.row.filePath" style="height: 30px; width: 30px;border-radius: 50%;" />
          </template>
        </el-table-column>
        <el-table-column width="200">
          <template slot-scope="scope">

            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align:center;padding-top: 20px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="1"
          :page-size="10" layout="total, prev, pager, next, jumper" :total="total"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        searchKey: '',
        currentPage4: 1,
        tableData: [],
        options: [],
        value: '',
        total: 0,
        pageNum: 0,
        pageSize: 15
      };
    },
    methods: {
      search() {

      },
      getStore() {
        var _this = this;
        var data = {
          pageNum: _this.pageNum,
          pageSize: _this.pageSize
        };

        this.axios
          .post('/api/v1/banner/query/page', data)
          .then(function(response) {
            if (response.data.code === 200) {
              _this.tableData = response.data.data.datas;
              _this.total = response.data.data.totalCount;
            }
          })
      },
      handleCurrentChange(val) {
        this.pageNum = `${val}` - 1
        this.getStore();
      },
      handleSizeChange(val) {
        this.pageSize = `${val}`
        this.getStore();
      },


      handleDelete(index, row) {
        let _this = this;
        this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            center: true
          })
          .then(() => {
            let data = {
              id: row.row.id
            }
            _this.axios
              .post('/api/v1/banner/delete', data)
              .then(function(response) {
                console.log(response.data.code === 200);
                if (response.data.code === 200) {
                  this.$message({
                    type: 'success',
                    message: '删除成功!'
                  });
                  _this.getStore();
                } else {
                  this.$message({
                    type: 'warn',
                    message: '删除失败'
                  });
                }
              })
              .catch(function(error) {
                this.$message({
                  type: 'info',
                  message: '删除失败'
                });
              });

          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
      },
      add() {
        this.$router.push({
          path: '/bannerAdd'
        });
      }
    },
    mounted: function() {
      this.getStore();
    }
  };
</script>

<style scoped="scoped">

  #operations {
    text-align: right;
  }
</style>



