<template>
	<div>
		<div class="breadcrumbs">
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item>
					<i class="el-icon-menu"></i>
					首页
				</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="dashboard-content">
			<div style="display: flex;">
				<div class="dashboard-item-card" v-for="(item, index) in itemCard" :key="index" :style="{ background: item.backgroundColors }">
					<div style="text-align: center;">
						<div style="font-weight: bold;">{{ item.num }}</div>
						<div>{{ item.unit }}</div>
					</div>
				</div>
			</div>
			<div style="display: flex;">
				<div class="charts-mode-50"><Four></Four></div>
				<div class="charts-mode-50"><Five></Five></div>
			</div>
			<div style="display: flex;">
				<div class="charts-mode"><One></One></div>
				<div class="charts-mode"><Two></Two></div>
				<div class="charts-mode"><Three></Three></div>
			</div>
		</div>
	</div>
</template>

<script>
import One from './One';
import Two from './Two';
import Three from './Three';
import Four from './Four';
import Five from './Five';
import Six from './Six';
export default {
	data() {
		return {
			BoxHeight: '',
			height: '',
			itemCard: [
				{
					backgroundColors: ' #18a689',
					num: '2000',
					unit: '盈亏(元)'
				},
				{
					backgroundColors: ' #99cc00',
					num: '2000',
					unit: '盈亏(元)'
				},
				{
					backgroundColors: '#3c9',
					num: '2000',
					unit: '盈亏(元)'
				},
				{
					backgroundColors: '#3b5999',
					num: '2000',
					unit: '盈亏(元)'
				},
				{
					backgroundColors: '#66cc99',
					num: '2000',
					unit: '盈亏(元)'
				},
				{
					backgroundColors: '#099',
					num: '2000',
					unit: '盈亏(元)'
				},
				{
					backgroundColors: '#f90',
					num: '2000',
					unit: '盈亏(元)'
				},
				{
					backgroundColors: '#09c',
					num: '2000',
					unit: '盈亏(元)'
				}
			]
		};
	},
	methods: {},
	created: function() {
		let height = document.body.clientHeight;
		this.BoxHeight = (height - 60) / 3;
		this.height = height;
	},
	components: {
		One: One,
		Two: Two,
		Three: Three,
		Four: Four,
		Five: Five,
		Six: Six
	}
};
</script>

<style>
.dashboard-content {
	background-color: #ffffff;
	width: 100%;
	height: 100%;
}
.charts-mode {
	width: 33%;
	padding-top: 40px;
}
.charts-mode-50 {
	width: 50%;
	padding-top: 40px;
}
.el-main {
	padding: 0 0 0 0;
	background: #475669;
}
.dashboard-item-card {
	height: 70px;
	width: 200px;
	border-radius: 5px;
	margin: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #ffffff;
}
</style>



